import mysql from 'mysql';
import 'babel-polyfill';

it('create connection', async () => {
    const connection = mysql.createConnection({
        host: 'localhost',
        user: 'mysql_user',
        password: 'mysql_password',
        database: 'test',
        port: 3306,
    });

    try {
        const db = await connection.connect();
    } catch (e) {
        expect(e).toBeTruthy();
    }
});
